___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  background-color: rgb(134, 134, 134);
  margin: 0;
}

header h1 {
  text-align: center;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0;
  text-transform: uppercase;
}

header h2 {
  text-align: center;
  font-weight: 100;
  margin-top: 0;
}

.game-board {
  display: flex;
  justify-content: center;
  align-items: center;
}

#game-status-field {
  color: white;
}

.game-board__board-wrapper {
  height: 350px;
  width: 350px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 5px;
}

.game-board__cell.game-board__cell--highlighted {
  color: white;
  text-shadow: 0 0 15px #ffffff;
}

.game-board__cell {
  display: flex;
  color: rgb(180, 180, 180);
  box-shadow: inset 15px 15px 10px rgb(20, 20, 20);
  justify-content: center;
  font-size: 5rem;
  background-color: rgb(37, 37, 37);
  border-radius: 10px;
  cursor: pointer;
  transition: 0.5s;
}

.game-board__cell:hover {
  box-shadow: 0 0 10px #ffffff;
}

.new-game-button {
  display: flex;
  justify-content: center;
  margin: 2rem 0;
}
.new-game-button__button {
  background-color: #424242;
  border-radius: 8px;
  border: 2px solid rgba(255, 255, 255, 0);
  box-sizing: content-box;
  font-family: "Roboto", sans-serif;
  color: #ffffff;
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  font-weight: 400;
  height: 40px;
  line-height: 20px;
  list-style: none;
  outline: none;
  padding: 8px 16px;
  text-align: center;
  text-decoration: none;
  transition: 100ms;
  user-select: none;
}

.new-game-button__button:hover {
  border: 2px solid white;
}

.new-game-button__button:active {
  background-color: rgb(255, 255, 255);
  color: rgb(114, 114, 114);
}

.win-status {
  display: grid;
  margin: auto;
  justify-items: center;
  align-items: center;
  text-align: center;
  width: 350px;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  font-size: 1rem;
}

.win-status > * {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.win-status__win-value {
  font-weight: 100;
  font-size: 2rem;
  color: white;
}

.win-status--grid-1 {
  border-right: 5px solid grey;
  border-bottom: 5px solid grey;
}
.win-status--grid-2 {
  border-left: 5px solid grey;
  border-bottom: 5px solid grey;
}
.win-status--grid-3 {
  border-right: 5px solid grey;
  border-top: 5px solid grey;
}
.win-status--grid-4 {
  border-left: 5px solid grey;
  border-top: 5px solid grey;
}

footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-size: 1rem;
}

@media (max-width: 400px) {
  /* Your styles for smaller screens here */
  .win-status {
    width: 200px;
  }
  .game-board__board-wrapper {
    height: 200px;
    width: 200px;
  }
  .game-board__cell {
    font-size: 3rem;
  }
}
